.hero {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: var(--space-20) var(--space-6);
    overflow: hidden;
}

/* Background Glows */
.bgGlow1 {
    position: absolute;
    top: -20%;
    left: -15%;
    width: 50vw;
    height: 50vw;
    max-width: 600px;
    max-height: 600px;
    background: radial-gradient(circle, rgba(249, 115, 22, 0.15) 0%, transparent 60%);
    filter: blur(80px);
    pointer-events: none;
}

.bgGlow2 {
    position: absolute;
    bottom: -20%;
    right: -15%;
    width: 40vw;
    height: 40vw;
    max-width: 500px;
    max-height: 500px;
    background: radial-gradient(circle, rgba(6, 182, 212, 0.1) 0%, transparent 60%);
    filter: blur(80px);
    pointer-events: none;
}

/* Floating Icons */
.floatingIcon {
    position: absolute;
    font-size: 2rem;
    opacity: 0.4;
    pointer-events: none;
}

/* Content */
.content {
    position: relative;
    z-index: 1;
    text-align: center;
    max-width: 800px;
}

/* Badge */
.badge {
    display: inline-flex;
    align-items: center;
    gap: var(--space-2);
    padding: var(--space-2) var(--space-4);
    background: rgba(34, 197, 94, 0.1);
    border: 1px solid rgba(34, 197, 94, 0.3);
    border-radius: var(--radius-full);
    font-size: 0.875rem;
    font-weight: 500;
    color: #4ade80;
    margin-bottom: var(--space-6);
}

.badgeDot {
    width: 8px;
    height: 8px;
    background: #22c55e;
    border-radius: 50%;
    animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {

    0%,
    100% {
        opacity: 1;
    }

    50% {
        opacity: 0.5;
    }
}

/* Headline */
.headline {
    font-size: clamp(2.5rem, 8vw, 4.5rem);
    font-weight: 800;
    font-family: var(--font-display);
    line-height: 1.1;
    margin: 0 0 var(--space-6) 0;
    color: var(--text-primary);
}

.dynamicWord {
    display: inline-block;
    background: linear-gradient(135deg, #f97316 0%, #fbbf24 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    min-width: 280px;
}

.cursor {
    -webkit-text-fill-color: #f97316;
    animation: blink 1s step-end infinite;
}

@keyframes blink {

    0%,
    50% {
        opacity: 1;
    }

    51%,
    100% {
        opacity: 0;
    }
}

/* Subtitle */
.subtitle {
    font-size: 1.25rem;
    color: var(--text-secondary);
    max-width: 550px;
    margin: 0 auto var(--space-8);
    line-height: 1.7;
}

.subtitle strong {
    color: var(--accent-orange);
    font-weight: 600;
}

/* CTAs */
.ctas {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--space-4);
    margin-bottom: var(--space-12);
    flex-wrap: wrap;
}

/* Stats */
.stats {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--space-10);
    margin-bottom: var(--space-8);
    flex-wrap: wrap;
}

.stat {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--space-1);
}

.statValue {
    font-size: 1.75rem;
    font-weight: 800;
    font-family: var(--font-display);
    color: var(--text-primary);
}

.statLabel {
    font-size: 0.8125rem;
    color: var(--text-muted);
}

/* Social Proof */
.socialProof {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--space-4);
    flex-wrap: wrap;
}

.avatarStack {
    display: flex;
}

.avatar {
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-dark-tertiary);
    border: 2px solid var(--bg-dark);
    border-radius: 50%;
    font-size: 1rem;
    margin-left: -10px;
}

.avatar:first-child {
    margin-left: 0;
}

.socialText {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--space-1);
}

.stars {
    display: flex;
    gap: 2px;
}

.socialText span {
    font-size: 0.875rem;
    color: var(--text-muted);
}

.socialText strong {
    color: var(--text-primary);
}

/* Responsive */
@media (max-width: 768px) {
    .hero {
        padding: var(--space-16) var(--space-4);
        min-height: auto;
    }

    .dynamicWord {
        min-width: auto;
        display: block;
    }

    .ctas {
        flex-direction: column;
        width: 100%;
    }

    .ctas button {
        width: 100%;
    }

    .stats {
        gap: var(--space-6);
    }

    .floatingIcon {
        font-size: 1.5rem;
        opacity: 0.2;
    }
}